.vjs-loading-spinner{border:none;opacity:0;visibility:hidden;-webkit-animation:vjs-spinner-fade-out 2s linear 1;animation:vjs-spinner-fade-out 2s linear 1;-webkit-animation-delay:2s;animation-delay:2s}.vjs-loading-spinner:before,.vjs-loading-spinner:after{border:none}.vjs-loading-spinner:after{background-image:url(https://assets.nflxext.com/en_us/pages/wiplayer/site-spinner.png);background-repeat:no-repeat;background-position-x:50%;background-position-y:50%;background-size:100%}.vjs-seeking .vjs-loading-spinner:after,.vjs-waiting .vjs-loading-spinner:after{-webkit-animation:vjs-spinner-spin 1.1s linear infinite, vjs-spinner-fade 1.1s linear 1 !important;animation:vjs-spinner-spin 1.1s linear infinite, vjs-spinner-fade 1.1s linear 1 !important;-webkit-animation-delay:2s;animation-delay:2s}.vjs-seeking .vjs-loading-spinner,.vjs-waiting .vjs-loading-spinner{opacity:1;visibility:visible;-webkit-animation:vjs-spinner-fade-in 2s linear 1;animation:vjs-spinner-fade-in 2s linear 1;-webkit-animation-delay:2s;animation-delay:2s}@-webkit-keyframes vjs-spinner-fade-in{0%{opacity:0;visibility:visible}100%{opacity:1;visibility:visible}}@keyframes vjs-spinner-fade-in{0%{opacity:0;visibility:visible}100%{opacity:1;visibility:visible}}@-webkit-keyframes vjs-spinner-fade-out{0%{opacity:1;visibility:visible}100%{opacity:0;visibility:visible}}@keyframes vjs-spinner-fade-out{0%{opacity:1;visibility:visible}100%{opacity:0;visibility:visible}}

